import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import DirectionsWalkIcon from '@mui/icons-material/DirectionsWalk';
import MapIcon from '@mui/icons-material/Map';
import Section from './section';

const Access = () => {
  const { haleo, akitsu } = useStaticQuery(graphql`
    query PageQuery {
      haleo: imageSharp(fluid: { originalName: { eq: "haleo.jpg" } }) {
        gatsbyImageData
      }
      akitsu: imageSharp(fluid: { originalName: { eq: "akitsu.jpg" } }) {
        gatsbyImageData
      }
    }
  `);

  return (
    <Section id='access' title='アクセス' bgcolor='#fff'>
      <Grid container spacing={4}>
        {[
          {
            isPremium: true,
            img: haleo,
            title: 'プレミアムレッスン',
            place: '代官山',
            text: 'HALEO代官山スタジオ',
            map: 'https://goo.gl/maps/hKbegf76gBpruhSF6',
            routes: ['東急東横線代官山駅から徒歩3分']
          },
          {
            img: akitsu,
            title: 'レッスン',
            place: '東村山',
            text: '誠真会館秋津道場',
            map: 'https://goo.gl/maps/ZgSFmemB7XU5ZqLA7',
            routes: ['JR新秋津駅から徒歩1分', '西武鉄道池袋線秋津駅から徒歩3分']
          }
        ].map(({ isPremium, img, title, place, text, map, routes }, i) => (
          <Grid item key={i} xs={12} sm={6}>
            <Card
              sx={{
                height: '100%'
              }}
            >
              <Box height={240}>
                <GatsbyImage
                  image={getImage(img)}
                  alt={title}
                  style={{
                    height: '100%'
                  }}
                />
              </Box>
              <CardContent>
                <Typography
                  variant='h6'
                  fontWeight='bold'
                  color={isPremium && '#f39800'}
                >
                  {title} <small>({place})</small>
                </Typography>
                <Typography>場所：{text}</Typography>
                <Button
                  variant='contained'
                  sx={{
                    my: 2,
                    bgcolor: '#f39800',
                    '&:hover': {
                      backgroundColor: '#fff',
                      color: '#f39800'
                    }
                  }}
                  startIcon={<MapIcon />}
                  onClick={() => {
                    window.open(map, '_blank', 'noreferrer');
                  }}
                >
                  Google マップ
                </Button>

                {routes.map((route, i) => (
                  <Typography key={i} my={1}>
                    <DirectionsWalkIcon /> {route}
                  </Typography>
                ))}
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Section>
  );
};

export default Access;
